@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  .terminal-scrollbar::-webkit-scrollbar {
    @apply w-[.75rem];
  }

  .terminal-scrollbar::-webkit-scrollbar-track {
    @apply bg-nota-black/[.75];
  }

  .terminal-scrollbar::-webkit-scrollbar-thumb {
    @apply bg-nota-gray/[.15] rounded-md transition-all border-[3px] 
      border-solid border-transparent bg-clip-content;
  }

  .terminal-link {
    @apply cursor-pointer text-nota-gray hover:underline;
  }

  .terminal-bold {
    @apply font-bold text-nota-green;
  }

  .terminal-commands {
    @apply text-nota-yellow;
  }

  .terminal-normal {
    @apply text-nota-blue;
  }
}

